<template>
  <div class="container">
        <h1>查询联系方式</h1>
        <div id="message-form">
          <button @click="submitClick">查询</button>
        </div>


        <div v-for="(item) in userList" :key="item">
          <p>用户：{{ item.attributes.account }}</p>
        </div>


    </div>

    
</template>

<script setup>
import { ref } from "vue";

 const userList = ref([])




  const AV = require("leancloud-storage");
  const { Query, User } = AV;

  AV.init({
    appId: "AltAzzf7BhiRzw109IUYNfJd-MdYXbMMI",
    appKey: "zNwXu2eLBIrkLBguzRM7q36p",
    serverURL: "https://altazzf7.api.lncldglobal.com",
  });

  const submitClick = ()=>{
      const query = new AV.Query("UserInfo");
      // 按创建时间降序排序，获取最新插入的数据
      query.descending('createdAt');
      // 设置查询限制，获取前10条数据
      query.limit(10);
      query.find().then((user) => {
          // user 是包含满足条件的 user对象的数组
          userList.value = user
      });   
  }
 
</script>

